<template>
    <div>
      <!-- 查询 -->
      <common-wrapper>
        <entry-management-search class="search"
                                 :warehouseList="warehouseList"
                                 :merchantsList="searchMerchantsList"
                                 :brandList="brandList"
                                 @getMerchants="getSearchMerchantsList"
                                 @getBrandModel="getBrandModel"
                                 @resetPage="resetPage"
                                 @search="queryList"
                                 @rest="rest" />
      </common-wrapper>
      <nav-button>
        <el-button-group>
          <el-button icon="fdddfont icon-add-new" @click="handleAdd">新增</el-button>
          <el-button icon="el-icon-delete" @click="handleDel" :disabled="isButtonIsDisabled">删除</el-button>
          <el-alert
            style="display: inline;height: 29px;line-height: 29px;margin-left: 10px;"
            v-if="flag==1"
            :closable="false"
            title="选中项包含审核已通过的收货单，请重新选择！"
            type="warning"
            show-icon>
          </el-alert>
          <!--<el-button icon="el-icon-edit-outline" @click="check" :disabled="!isButtonIsDisabled()">审核</el-button>-->
        </el-button-group>
      </nav-button>
      <!-- 收货单列表 -->
      <common-wrapper>
        <common-table :flex="270">
          <template slot="table">
            <el-table
              ref="orderTable"
              height="100%"
              stripe
              :border="true"
              tooltip-effect="light"
              v-loading="isListLoading"
              :data="tableData"
              @selection-change="selected => selectRow = selected">
              <el-table-column type="selection" width="30"></el-table-column>
              <el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>
              <el-table-column sortable prop="goodsReceiveUuid" label="收货单号" min-width="150"></el-table-column>
              <el-table-column sortable prop="goodsReceiveMerchant" label="商户"></el-table-column>
              <el-table-column sortable prop="goodsReceiveDate" label="送达日期" min-width="120"></el-table-column>
              <el-table-column prop="goodsReceviceAuditing" label="状态"  :formatter="formatState"></el-table-column>
              <el-table-column sortable prop="goodsWarehouseUuid" label="仓库" :formatter="formatWarehouse"></el-table-column>
              <el-table-column prop="goodsReceiveRemark" label="备注" min-width="150" :show-overflow-tooltip="true"></el-table-column>
              <el-table-column label="操作" width="190" >
                <template slot-scope="scope">
                  <el-tooltip class="item" effect="light" content="查看" placement="bottom">
                    <el-button size="mini" round icon="el-icon-view" @click.stop="handleScan(scope.row)"></el-button>
                  </el-tooltip>
                  <el-tooltip class="item" effect="light" content="编辑" placement="bottom">
                    <el-button size="mini" round icon="fdddfont icon-modify" v-if="showEdit(scope.row)"  @click.stop="handleEdit(scope.row)"></el-button>
                  </el-tooltip>
                  <el-tooltip class="item" effect="light" :content="scope.row.goodsReceviceAuditing == 1 ? '审核' :'提交审核'" placement="bottom">
                    <el-button size="mini" round icon="el-icon-tickets" v-if="showCheck(scope.row)"  @click.stop="check(scope.row)"></el-button>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
          </template>
          <template slot="pagination">
            <el-pagination   background   layout="total, prev, pager, next"
                             :current-page.sync="pagination.pageNum"
                             :page-size="pagination.pageSize"
                             :total="pagination.total"
                             @current-change="changePage"
            ></el-pagination>
          </template>
        </common-table>
      </common-wrapper>
      <!-- 新增收货单-->
      <order-create
        :show.sync="isCreateFormVisible"
        :openType="openType"
        :selectedOrderInfo="selectedOrderInfo"
        :warehouseList="warehouseList"
        :merchantsList="merchantsList"
        :searchParams="searchParams"
        @search="queryList"
      />
      <!-- 收货单详情 -->
      <order-detail
        :show.sync="isDetailFormVisible"
        :selectedOrderInfo="selectedOrderInfo"
        :warehouseList="warehouseList"
        :merchantsList="merchantsList"
        :searchParams="searchParams"
        @rest="rest"
      />
      <ensure-check
        :show.sync="isCheckFormVisible"
        :checkInfo="checkInfo"
        @updateTable="updateTable"
      />
    </div>
</template>

<script>
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import NavButton from '@/components/navButton'
import EntryManagementSearch from './Components/Search'
import OrderCreate from './Components/OrderCreate'
import OrderDetail from './Components/OrderDetail'
import EnsureCheck from './Components/EnsureCheck'
import moment from 'moment'
import { setSession, getSession } from '@/utils/session'
// api
import { getOrgTree } from '@/api/organization'
import { getEntryOrder, checkEntryOrder, deleteEntryOrder, ensureCheckEntry } from '@/api/order'
import { getWarehouseList } from '@/api/warehouse'
import { getAllMerchantsList,getBrandModel } from '@/api/common'
import {mixins} from '@/mixins'

export default {
  name: 'EntryManger',
  mixins: [mixins],
  data () {
    return {
      warehouseList: this.$store.getters.warehouseList,
      merchantsList: [],
      brandList: [],
      modelBrand: [],
      modelList: [],
      tableData: [],
      selectRow: [], // 列表选中列
      selectedOrderInfo: {},
      isListLoading: false, // 列表加载
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      searchParams: {}, // 查询参数
      isCreateFormVisible: false,
      openType: 1,
      isCheckFormVisible: false,
      checkInfo: {},
      isDetailFormVisible: false,
      searchMerchantsList: [],
      flag: 2
    }
  },
  mounted () {
    let params = {warehouseUuid:getSession('warehouseUuid')}
    this.getMerchantsList(params)
    this.getSearchMerchantsList(params)
    this.getBrandModel(params)
  },
  computed:{
    isButtonIsDisabled () {
      this.flag = 2
      if(this.selectRow.length){
        try {
          this.selectRow.forEach(item => {
            if (item.goodsReceviceAuditing == '2') {
              this.flag = 1
              throw new Error("selectErr")
            } else {
              this.flag = 0
            }
          })
        }catch (e) {
          if(e.message!="selectErr") throw e
        }
      }else{
        this.flag = 2
      }
      if(this.flag == 0){
        return false
      }else{
        return true
      }
    }
  },
  methods: {
    // 查询和初始化查询
    queryList (params) {
      const searchParams = this.searchParams = params
      this.getMerchantsList({warehouseUuid:searchParams.warehouseUuid})
      this.getOrders(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    rest (params) {
      const searchParams = this.searchParams = params
      this.getOrders(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.queryList(this.searchParams)
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
    resetPage () {
      this.pagination = { // 翻页数据
          pageNum: 1,
          pageSize: 10,
          total: 0
      }
    },
    updateTable () {
      this.queryList(this.searchParams)
    },
    // 处理收货单状态
    formatState (row) {
      switch(row.goodsReceviceAuditing){
        case '0':
          return '待提交'
          break
        case '1':
          return '待审核'
          break
        case '2':
          return '审核通过'
          break
        case '3':
          return '审核未通过'
          break
        case '4':
          return '完成收货'
          break
        default:
          return '未知'
      }
    },
    formatWarehouse (row) {
      if (row.goodsWarehouseUuid) {
        let result = this.warehouseList.find(item => item.warehouseUuid === row.goodsWarehouseUuid)
        if(result){
          return result.warehouseName
        }
      }
    },
    // 获取收货单列表
    getOrders (params) {
      this.isListLoading = true
      getEntryOrder(params).then(res => {
        if(res.item.length == 0 && res.pageNum > 1){
          this.pagination.pageNum = 1
          params.pageNum = 1
          this.getOrders (params)
          return
        }
        this.tableData = res.item
        for(let order of this.tableData){
          order.goodsReceiveDate = moment(order.goodsReceiveDate).utcOffset(8).format('YYYY-MM-DD')
        }
        this.pagination.total = res.total
        this.pagination.pageNum = res.pageNum
        this.pagination.pageSize = res.pageSize
        this.isListLoading = false
      })
    },
    // 商户列表
    getMerchantsList (params) {
      return new Promise(resolve => {
        getAllMerchantsList(params).then(res => {
          if(!res.error) {
            this.merchantsList = res
            resolve()
          }
        })
      })
    },
    getSearchMerchantsList(params){
      getAllMerchantsList(params).then(res => {
        this.searchMerchantsList = res
      })
    },
    // 获取品牌型号列表
    getBrandModel (params) {
      getBrandModel(params).then(res => {
        if (!res.error) {
          this.brandList = []
          this.modelList = []
          res.forEach((item,index) => {
            this.brandList.push(item.productBrand)
            item.productModel.forEach(item2 => {
              this.modelList.push(item2)
            })
          })
          this.modelBrand = res
        }
      })
    },
    handleAdd () {
      this.isCreateFormVisible = true
      this.openType = 1
    },
    handleDel () {
      this.$confirm('确认删除选中的收货单吗?', '提示', {
        type: 'warning'
      }).then(() => {
        let params = {
          orderAccount: []
        }
        for(let item of this.selectRow){
          params.orderAccount.push(item.goodsReceiveUuid)
        }
        deleteEntryOrder(params).then(res => {
          if(!res.error){
            this.$message({
              message: '删除成功',
              type: 'success'
            })
            this.queryList(this.searchParams)
          }
        })
      }).catch(() => {})
    },
    handleScan (data) {
      this.isDetailFormVisible = true
      this.selectedOrderInfo = data
    },
    handleEdit (data) {
      this.isCreateFormVisible = true
      this.openType = 3
      this.selectedOrderInfo = data
    },
    showCheck (row) {
      if (row.goodsReceviceAuditing === '0' || row.goodsReceviceAuditing === '1' || row.goodsReceviceAuditing === '3') {
        return true
      } else {
        return false
      }
    },
    showEdit(row) {
      if (row.goodsReceviceAuditing === '2') {
        return false
      } else {
        return true
      }
    },
    // 审核
    check (row) {
      if (row.goodsReceviceAuditing === '0' || row.goodsReceviceAuditing === '3') {
        let params = {
          orderAccount: [row.goodsReceiveUuid],
          checkStatus:  1
        }
        this.$confirm('确认将该收货单提交审核吗?', '提示', {
          type: 'info'
        }).then(() => {
          checkEntryOrder(params).then(res => {
            if(!res.error){
              this.$message({
                message: '收货单已提交审核',
                type: 'success'
              })
              this.queryList(this.searchParams)
            }
          })
        }).catch(() => {})
      } else if (row.goodsReceviceAuditing === '1') {
        this.isCheckFormVisible = true
        this.checkInfo = row
      }
    }
  },
  components: {
    CommonWrapper,
    CommonTable,
    NavButton,
    EntryManagementSearch,
    OrderCreate,
    EnsureCheck,
    OrderDetail
  }
}
</script>

<style scoped>

</style>



