<template>
  <el-form :model="searchForm" ref="searchForm"  label-width="50px">
    <el-row>
      <el-col :span="4">
        <el-form-item label="仓库" prop="warehouseUuid">
          <el-select v-model="searchForm.warehouseUuid" class="w100p" filterable  @change="getMerchantsList">
            <el-option
              v-for="(org, index) in warehouseList"
              :key="index"
              :label="org.warehouseName"
              :value="org.warehouseUuid"></el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="4">
        <el-form-item label="商户"  prop="merchantUuid" >
          <el-select v-model="searchForm.merchantUuid" filterable clearable @change="getBrandList" class="w100p" filterable placeholder="请选择商户">
            <el-option
              v-for="(item, index) in merchantsList"
              :key="index"
              :label="item.userName"
              :value="item.userUuid"
            />
          </el-select>
        </el-form-item>
      </el-col>
      <!--<el-col :span="4">-->
        <!--<el-form-item label="品牌" prop="productBrand">-->
          <!--<el-select clearable filterable placeholder="请选择品牌"   v-model="searchForm.productBrand" >-->
            <!--<el-option-->
              <!--v-for="(item, index) in brandList"-->
              <!--:key="index"-->
              <!--:label="item"-->
              <!--:value="item"-->
            <!--&gt;</el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
      <!--</el-col>-->
      <el-col :span="5">
        <el-form-item label="收货单号" label-width="80px" prop="receiveOrderId">
          <el-input v-model="searchForm.receiveOrderId" maxlength="200" placeholder="请输入收货单号"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="4">
        <el-form-item label="状态" prop="orderStatus">
          <el-select v-model="searchForm.orderStatus" filterable clearable class="w100p">
            <el-option class="radio" value="0" label="待提交"></el-option>
            <el-option class="radio" value="1" label="待审核"></el-option>
            <el-option class="radio" value="2" label="审核通过"></el-option>
            <el-option class="radio" value="3" label="审核未通过"></el-option>
          </el-select>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="6">
        <el-form-item label="送达日期(起)" label-width="100px" prop="startDate">
          <el-date-picker
            class="w100p"
            v-model="searchForm.startDate"
            type="date"
            align="right"
            clearable
            unlink-panels
            placeholder="请选择送达日期(起)"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
            size="mini"
          >
          </el-date-picker>
        </el-form-item>
      </el-col>
      <el-col :span="6">
        <el-form-item label="送达日期(止)" label-width="100px" prop="endDate">
          <el-date-picker
            class="w100p"
            v-model="searchForm.endDate"
            type="date"
            align="right"
            clearable
            unlink-panels
            placeholder="请选择送达日期(止)"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
            size="mini"
          >
          </el-date-picker>
        </el-form-item>
      </el-col>
      <el-col :span="4" :offset="2" class="flex-right">
        <el-button type="primary" @click.native="search">查询</el-button>
        <el-button @click="resetForm('searchForm')">重置</el-button>
      </el-col>
    </el-row>
  </el-form>
</template>

<script>
  import { setSession, getSession } from '@/utils/session'
  import moment from 'moment'

  export default {
  name: 'EntryManagementSearch',
  data () {
    return {
      // 检索界面数据
      searchForm: {
        warehouseUuid: '',
        receiveOrderId: '',
        merchantUuid: '',
        productBrand: '',
        goodsReceiveDate: '',
        orderStatus: '',
        startDate:'',
        endDate:''
      }
    }
  },
  props: {
    warehouseList: {
      type: Array
    },
    merchantsList: {
      type: Array
    },
    brandList: {
      type: Array
    }
  },
  mounted () {
    this.searchForm.warehouseUuid = getSession('warehouseUuid')
    this.$emit("search", Object.assign({}, this.searchForm))
    this.getMerchantsList(getSession('warehouseUuid'))
  },
  methods: {
    getMerchantsList (value) {
      this.searchForm.merchantUuid = ''
      this.searchForm.productBrand = ''
      this.searchForm.goodsReceiveMerchantId = ''
      setSession('warehouseUuid',value)
      this.$emit('getMerchants', {warehouseUuid:value})
      this.$emit('getBrandModel', {warehouseUuid:value})
      this.$emit("search", Object.assign({}, this.searchForm))
    },
    getBrandList () {
      this.searchForm.productBrand = ''
      this.$emit('getBrandModel', {warehouseUuid:this.searchForm.warehouseUuid,merchantUuid:this.searchForm.merchantUuid})
    },
    // 检索
    search () {
      if (this.searchForm.endDate && this.searchForm.startDate && ( moment(this.searchForm.endDate).isBefore(this.searchForm.startDate) || moment(this.searchForm.endDate).isSame(this.searchForm.startDate))) {
        this.$message({
          message: '送达日期(止)不能早于送达日期(起)，请重新选择！',
          type: 'warning'
        })
        this.searchForm.endDate = null
      } else {
        this.$emit('search', Object.assign({}, this.searchForm))
      }
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.searchForm.warehouseUuid = getSession('warehouseUuid')
      this.$emit('rest', Object.assign({}, this.searchForm))
    }
  }
}
</script>

<style lang="scss" scoped>
  .el-form-item{
    margin-bottom:10px;
  }
</style>



